import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import {
  MdIconModule, MdCardModule, MdButtonModule, MdListModule, MdProgressBarModule, MdMenuModule,
  MaterialModule, MdToolbarModule, MdTabsModule
} from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';

import { ChartsModule } from 'ng2-charts/ng2-charts';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';

import { GroupsComponent } from './groups.component';
import { GroupsRoutes } from './groups.routing';
import { GroupInstanceComponent } from './group-instance/group-instance.component';
import {DialogService} from '../shared/dialog-service/dialog.service';
import {FormsModule} from '@angular/forms';
import {AgmCoreModule} from 'angular2-google-maps/core';

@NgModule({
  imports: [
    MaterialModule,
    CommonModule,
    RouterModule.forChild(GroupsRoutes),
    MdIconModule,
    MdCardModule,
    MdButtonModule,
    MdListModule,
    MdProgressBarModule,
    MdMenuModule,
    ChartsModule,
    NgxDatatableModule,
    FlexLayoutModule,
    CommonModule,
    MdCardModule,
    MdToolbarModule,
    MdTabsModule,
    FormsModule,
    FlexLayoutModule,
    AgmCoreModule
  ],
  declarations: [
      GroupsComponent,
      GroupInstanceComponent
  ],
  exports: [
  ],
  entryComponents: [
      GroupInstanceComponent
  ],
  providers: [
    DialogService
  ]
})

export class GroupsModule {}



