import {Component, OnInit} from '@angular/core';
import {BaseApiService} from '../../shared/api-service/base-api.service';
import {Group} from '../../models/group';
import {MdDialogRef} from '@angular/material';

@Component({
    selector: 'app-monitor-group',
    templateUrl: './group-instance.component.html',
    styleUrls: ['./group-instance.component.scss'],
    providers: [BaseApiService]
})
export class GroupInstanceComponent {
    public name: '';
    public password: '';
    public dialog: MdDialogRef<GroupInstanceComponent>;

    constructor(private apiService: BaseApiService) {}
    public add() {
        this.apiService.post('', {
            'type': 'group',
            'name': this.name,
            'password': this.password
        }).subscribe(response => {
            console.log(response);
            this.dialog.close(response);
        }, error => {
            console.log(error);
            this.dialog.close(error);
        });
    }
    public cancel() {
        this.dialog.close(undefined);
    }
}



