import {Component, OnInit, NgZone} from '@angular/core';
import {Group} from './../models/group';
import {PouchdbService} from '../shared/pouchdb-service/pouchdb.service';
import {BaseApiService} from '../shared/api-service/base-api.service';
import {DialogService} from '../shared/dialog-service/dialog.service';
import {MdDialog, MdDialogConfig, MdDialogRef} from '@angular/material';
import {GroupInstanceComponent} from './group-instance/group-instance.component';
import {isUndefined} from 'util';

@Component({
    selector: 'app-groups',
    providers: [PouchdbService, BaseApiService],
    templateUrl: './groups.component.html',
    styleUrls: ['./groups.component.scss'],
    entryComponents : [GroupInstanceComponent]
})
export class GroupsComponent implements OnInit {

    public groups = new Array<Group>();
    public dialogRef: MdDialogRef<GroupInstanceComponent>;

    constructor(private dialogsService: DialogService,
                public dialog: MdDialog,
                private apiService: BaseApiService) {
    }

    ngOnInit() {
        this.getGroups();
    }

    public getGroups() {
        this.apiService.get('_design/groups/_view/all').subscribe(result => {
            console.log(result.rows);
            this.groups = result.rows;
        }, error => {
            console.error('error');
        });
    }
    public deleteGroup(row: any, $event) {
        this.dialogsService
            .confirm('Delete group ', 'Are you sure you want to delete ' + row.value.name + '?')
            .subscribe(res => {
                if (res) {
                    this.apiService.del(row.id + '?rev=' + row.value._sync.rev).subscribe(result => {
                            console.log(result);
                            this.groups.splice(row.$$index, 1);
                        }, error => {
                            console.error('error');
                        }
                    );
                }
            });
    }
    public addGroup() {
        const config: MdDialogConfig =  {
            disableClose: false,
            width: '400',
            height: '900',
            position: {
                top: '',
                bottom: '',
                left: '',
                right: ''
            }
        };
        this.dialogRef = this.dialog.open(GroupInstanceComponent, config);
        this.dialogRef.componentInstance.dialog = this.dialogRef;
        this.dialogRef.afterClosed().subscribe(result => {
            console.log(result);
            if (!isUndefined(result)) {
                this.getGroups();
            }
        });
    }

}



